import React from 'react';
import {Switch, Route, useRouteMatch} from "react-router-dom";
import Loading from "../../components/loading";
import Loadable from "react-loadable";
import Header from "../../components/header";

export default function Main(props) {
    let { path} = useRouteMatch();
    return(
        <div>
            <Header/>

            <div>
                <Switch>
                    <Route exact path={`${path}/`}>
                        <AsyncStocks/>
                    </Route>
                    <Route path={`${path}/home`}>
                        <AsyncHome/>
                    </Route>

                    <Route path={`${path}/dashboard`}>
                        <AsyncDashboard/>
                    </Route>

                    <Route path={`${path}/my-portfolios`}>
                        <AsyncPortfolio/>
                    </Route>

                    <Route path={`${path}/EditPortfolio/portfolio/edit`}>
                        <AsyncEditPortfolio/>
                    </Route>
                    <Route path={`${path}/screener`}>
                        <AsyncScreener/>
                    </Route>

                    <Route path={`${path}/discover`}>
                        <AsyncDiscover/>
                    </Route>

                    <Route path={`${path}/activationStrategy`}>
                        <AsyncActivationStrategy/>
                    </Route>

                    <Route path={`${path}/strategyList`}>
                        <AsyncStrategyList/>
                    </Route>

                    <Route path={`${path}/backTest`}>
                        <AsyncBackTest/>
                    </Route>

                    <Route path={`${path}/newStrategy`}>
                        <AsyncNewStrategy/>
                    </Route>

                    <Route path={`${path}/plan`}>
                        <AsyncPlan/>
                    </Route>
                </Switch>
            </div>
        </div>
    )
}


const LoadingComponent = ({isLoading, error}) => {
    // Handle the loading state
    if (isLoading) {
        return <Loading/>;
    }
    // Handle the error state
    else if (error) {
        return <div>Sorry, there was a problem loading the page.</div>;
    } else {
        return null;
    }
};

const AsyncHome= Loadable({loader: () => import('../../Information/Information'),loading:LoadingComponent});
const AsyncDashboard= Loadable({loader: () => import('../../Dashboard/dashboard'),loading:LoadingComponent});
const AsyncPortfolio= Loadable({loader: () => import('../../containers/portfolio/portfolio'),loading:LoadingComponent});
const AsyncEditPortfolio= Loadable({loader: () => import('../../containers/portfolio/editPortfolio'),loading:LoadingComponent});
const AsyncStocks= Loadable({loader: () => import('../../containers/stocks'),loading:LoadingComponent});
const AsyncScreener= Loadable({loader: () => import('../../containers/screener/screener'),loading:LoadingComponent});
const AsyncDiscover= Loadable({loader: () => import('../../containers/discover/discover'),loading:LoadingComponent});
const AsyncActivationStrategy= Loadable({loader: () => import('../../containers/Strategy/ActivationStrategy'),loading:LoadingComponent});
const AsyncStrategyList= Loadable({loader: () => import('../../containers/Strategy/strategyList'),loading:LoadingComponent});
const AsyncBackTest= Loadable({loader: () => import('../../containers/Strategy/backTest'),loading:LoadingComponent});
const AsyncNewStrategy= Loadable({loader: () => import('../../containers/Strategy/newStrategy'),loading:LoadingComponent});
const AsyncPlan= Loadable({loader: () => import('../../containers/Strategy/Box'),loading:LoadingComponent});
